import logo from './logo.svg';
import './App.css';

import './css/index.css';
import './css/media.css';
import './css/animation.css';

import Header from './components/Header';
import Hero from './components/Hero';
import About from './components/About';
import Skills from './components/Skills';
import Portfolio from './components/Portfolio';
import Contacts from './components/Contacts';

import React, { useState, useEffect } from 'react';
import { Link, animateScroll as scroll } from "react-scroll";

import { YMInitializer } from 'react-yandex-metrika';

function App() {

  useEffect(() => {
    let aboutTop, skillsTop, portfolioTop, appear = false;
    document.body.style.cssText = `--scrollTop: ${window.pageYOffset || document.documentElement.scrollTop}; --windowHeight: ${document.documentElement.clientHeight};`;
    const getCoords = (elem) => {
      const box = elem.getBoundingClientRect();
      return {
        top: box.top + window.pageYOffset,
      }
    }
    aboutTop = getCoords(document.querySelector(".about"));
    document.querySelector(".about").style.cssText = `--scrollTopAbout: ${aboutTop.top}`;
    skillsTop = getCoords(document.querySelector(".skills"));
    document.querySelector(".skills").style.cssText = `--scrollTopSkills: ${skillsTop.top}`;
    portfolioTop = getCoords(document.querySelector(".portfolio"));
    document.querySelector(".portfolio").style.cssText = `--scrollTopPortfolio: ${portfolioTop.top}`;

    window.addEventListener('resize', function(event) {
      aboutTop = getCoords(document.querySelector(".about"));
      document.querySelector(".about").style.cssText = `--scrollTopAbout: ${aboutTop.top}`;
      skillsTop = getCoords(document.querySelector(".skills"));
      document.querySelector(".skills").style.cssText = `--scrollTopSkills: ${skillsTop.top}`;
      portfolioTop = getCoords(document.querySelector(".portfolio"));
      document.querySelector(".portfolio").style.cssText = `--scrollTopPortfolio: ${portfolioTop.top}`;
    }, true);

    window.addEventListener('scroll', e => {
      document.body.style.cssText = `--scrollTop: ${window.pageYOffset || document.documentElement.scrollTop}; --windowHeight: ${document.documentElement.clientHeight};`;
      if (+(window.pageYOffset || document.documentElement.scrollTop) - +skillsTop.top + 0.5 * document.documentElement.clientHeight > 0 && !appear) {
        document.querySelectorAll(".skills__secondary-skill").forEach((element) => { element.classList.add('active') });
        appear = true;
      }
    });

  }, []);
  document.title = "Портфолио - Косарев Михаил";
  const [mobileMenuIsOpen, setMobileMenuIsOpen] = useState(false);
  return (
    <div className=''>
    <div id="nav-icon3">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
      { mobileMenuIsOpen ? (<button className='burger-close' onClick={() => setMobileMenuIsOpen(false)}>
        <svg width="26" height="26" viewBox="0 0 26 26" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M25 25L1 1M25 1L1 25" stroke="black" strokeWidth="2" strokeLinecap="round"/>
        </svg>

      </button>) : (
        <button className='burger' onClick={() => setMobileMenuIsOpen(true)}>
          <svg width="33" height="20" viewBox="0 0 33 20" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect width="33" height="3.83721" fill="black"/>
          <rect y="7.67383" width="33" height="3.83721" fill="black"/>
          <rect y="15.3496" width="33" height="3.83721" fill="black"/>
          </svg>
        </button>
      ) }

          <div className={'burger-menu' + (mobileMenuIsOpen ? ' active' : '')}>
          <ul className='header__sections-list'>
            <li>
              <a href='https://github.com/origiboy' target='_blank' className='header__section header__section-github' onClick={() => setMobileMenuIsOpen(false)}>
                GitHub
              </a>
            </li>
            <li>
            <Link
              activeClass="active"
              to="section-about"
              spy={true}
              smooth={true}
              offset={-70}
              duration={500}
              className='header__section'
              onClick={() => setMobileMenuIsOpen(false)}
            >
              Обо мне
            </Link>

            </li>
            <li>
              <Link
                activeClass="active"
                to="section-skills"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
                className='header__section'
                onClick={() => setMobileMenuIsOpen(false)}
              >
                Навыки
              </Link>
            </li>
            <li>
              <Link
                activeClass="active"
                to="section-portfolio"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
                className='header__section'
                onClick={() => setMobileMenuIsOpen(false)}
              >
                Портфолио
              </Link>
            </li>
            <li>
              <Link
                activeClass="active"
                to="section-contacts"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
                className='header__section'
                onClick={() => setMobileMenuIsOpen(false)}
              >
                Контакты
              </Link>
            </li>
          </ul>
          </div>
        <Header />
        <Hero />
        <div className='main'>
          <div className='main__conainer'>
            <div className='main__progress'>
              <div className='main__line'></div>
              <button className='progress__button'></button>
            </div>
            <About />
            <Skills />
             <Portfolio />
           <Contacts />
          </div>
        </div>
      <div className='contacts__date'>{  new Date().getFullYear() }</div>
      <YMInitializer accounts={[73968901]} options={{webvisor: true}}/>
    </div>
  );
}

export default App;
